import './style.css';
import LayerTemplate from './LayerTemplate';
import AddNewLayerItems from './AddNewLayerItems';

const layers = document.querySelector('.layers-wrap');
const layersCol = [];

const nextElemId = {
  id: 1,
};

const init = () => {
  const layer = {};

  layer.elem = new LayerTemplate({
    numb: nextElemId.id,
  }).elem;

  layer.items = document.querySelector(`.layer-${nextElemId.id}`).querySelectorAll('.frame-item');

  layer.elem.classList.add('selected-layer');

  document.querySelector('.frame-wrap').id = `frame-item-${nextElemId.id}`;

  document.querySelectorAll('.frame-item').forEach((elem) => {
    /* eslint-disable */
    elem.style.background = 'grey';
    /* eslint-enable */
  });

  nextElemId.id += 1;

  layersCol.push(layer);
  layers.appendChild(layer.elem);
};

init();

const chooseBucket = {
  enable: false,
  color: '',
};

const selectBucket = (bucket) => {
  if (!chooseBucket.enable) {
    bucket.classList.add('show-tool');
    chooseBucket.enable = true;
    document.body.style.cursor = 'all-scroll';
  } else {
    bucket.classList.remove('show-tool');
    chooseBucket.enable = false;
    document.body.style.cursor = 'default';
  }
};

const selectColor = (elem, color) => {
  chooseBucket.color = color;
  document.querySelectorAll('.colors').forEach((item) => {
    item.classList.remove('show-tool');
  });
  elem.classList.add('show-tool');
};

const paint = (elem) => {
  /* eslint-disable */
  if (chooseBucket.enable) elem.style.background = chooseBucket.color;
  /* eslint-enable */
};


const addNewLayer = () => {
  if (layersCol.length === 4) return;

  const layer = {};

  const frameWrap = new AddNewLayerItems(`${nextElemId.id}`).elem;

  document.querySelector('.frame').appendChild(frameWrap);

  layer.elem = new LayerTemplate({
    numb: nextElemId.id,
  }).elem;

  layer.items = document.querySelector(`.layer-${nextElemId.id}`).querySelectorAll('.frame-item');

  nextElemId.id += 1;

  layersCol.push(layer);
  layers.appendChild(layer.elem);
};

/*
  исправить все id
*/
const checkId = () => {
  /* eslint-disable */
  nextElemId.id = 1;

  layersCol.forEach((layer) => {
    layer.elem.id = `layer-${nextElemId.id}`;
    layer.elem.querySelector('.layers-item-numb').innerText = nextElemId.id;

    layer.items[0].parentNode.id = `frame-item-${nextElemId.id}`;

    nextElemId.id += 1;
  });
  /* eslint-enable */
};

const deleteLayer = (layerId) => {
  if (layersCol.length === 1) return;

  layersCol.forEach((layer, i) => {
    if (layer.elem.id === layerId) {
      layersCol.splice(i, 1);
    }
  });

  /*
    удалить слой из DOM
   */
  document.querySelectorAll('.layers-item').forEach((item) => {
    if (item.id === layerId) document.querySelector('.layers-wrap').removeChild(item);
  });

  /*
    удалить фрейм из DOM
   */
  document.querySelector('.frame').removeChild(document.querySelector(`.${layerId}`));

  checkId();
};

const selectLayer = (selectedLayer) => {
  layersCol.forEach((layer) => {
    if (layer.elem.classList.contains('selected-layer')) layer.elem.classList.remove('selected-layer');
  });
  selectedLayer.classList.add('selected-layer');

  document.querySelectorAll('.frame-wrap').forEach((frame) => {
    frame.classList.add('visibility-frame-layer');
    if (frame.classList.contains(selectedLayer.id)) frame.classList.remove('visibility-frame-layer');
  });
};


/* eslint-disable */
const canvas = document.getElementById('canvas');
const context = canvas.getContext('2d');

let count = 0;
let showLayer = count % layersCol.length;
let fps = 1000;

const animCanvas = () => {
  let top = {
    x: 7,
    y: 7,
    w: 90,
  };
  let middle = {
    x: 7,
    y: 104,
    w: 90,
  };
  let bottom = {
    x: 7,
    y: 204,
    w: 90,
  };

  layersCol[count % layersCol.length].items.forEach((elem, j) => {
    if (j + 1 < 4) {
      context.fillStyle = elem.style.background;
      context.fillRect(top.x, top.y, top.w, top.w);
      top.x = top.x + top.w + 7;
    } else if (j + 1 >= 4 && j + 1 < 7) {
      context.fillStyle = elem.style.background;
      context.fillRect(middle.x, middle.y, middle.w, middle.w);
      middle.x = middle.x + middle.w + 7;
    } else {
      context.fillStyle = elem.style.background;
      context.fillRect(bottom.x, bottom.y, bottom.w, bottom.w);
      bottom.x = bottom.x + bottom.w + 7;
    }
  });
};

let idx = setInterval(() => {
  animCanvas();
  count++;
  showLayer = count % layersCol.length;
}, fps);


const setFps = (fpsButton) => {
  clearInterval(idx);
  fps = 1000 / parseFloat(fpsButton);

  idx = setInterval(() => {
    animCanvas();
    count++;
    showLayer = count % layersCol.length;
  }, fps);
};
/* eslint-enable */

document.addEventListener('click', (event) => {
  if (event.target.classList.contains('add-layer')) addNewLayer();
  if (event.target.classList.contains('layers-item-del')) deleteLayer(event.target.parentNode.id);
  if (event.target.classList.contains('bucket')) selectBucket(event.target);
  if (event.target.classList.contains('colors')) selectColor(event.target, event.target.textContent);
  if (event.target.classList.contains('frame-item')) paint(event.target);
  if (event.target.classList.contains('fps-item')) setFps(event.target.textContent);
  if (event.target.classList.contains('layers-item')) selectLayer(event.target);
});
