export default class LayerTemplate {
  constructor(data) {
    this.elem = LayerTemplate.createLayer(data);
  }

  static createLayer(data) {
    const div = document.createElement('div');
    div.classList.add('layers-item');
    div.id = `layer-${data.numb}`;

    div.innerHTML = `<div class="layers-item-numb">${data.numb}</div><div class="layers-item-del">del</div><div class="layers-item-copy">cop</div><div class="layers-item-move">mv</div>`;

    return div;
  }
}
