export default class AddNewLayerItems {
  constructor(data) {
    this.elem = AddNewLayerItems.createFrameItem(data);
  }

  static createFrameItem(data) {
    const div = document.createElement('div');
    div.classList.add('frame-wrap');
    div.classList.add(`layer-${data}`);
    div.classList.add('visibility-frame-layer');
    div.id = `frame-item-${data}`;

    div.innerHTML = `
      <div class="frame-item" style="background: grey"></div>
      <div class="frame-item" style="background: grey"></div>
      <div class="frame-item" style="background: grey"></div>
      <div class="frame-item" style="background: grey"></div>
      <div class="frame-item" style="background: grey"></div>
      <div class="frame-item" style="background: grey"></div>
      <div class="frame-item" style="background: grey"></div>
      <div class="frame-item" style="background: grey"></div>
      <div class="frame-item" style="background: grey"></div>
    `;

    return div;
  }
}
